<template>
    <div class="siteSetting">
        <div class="box">
            <p class="title">站点设置</p>
            <div class="tableCOn" style="padding-top: 20px;">

                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>当前线路：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-dropdown class="select_"  @command="changeLine">
                            <span class="el-dropdown-link">
                                {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                            </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </el-col>
                </el-row>

                
                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>当前站点：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-dropdown class="select_"  @command="changeSite">
                            <span class="el-dropdown-link">
                                {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                            </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </el-col>
                </el-row>


                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>当前城市：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-cascader v-model="chooseCity" :change="changeCity" :options="city" :show-all-levels="true"></el-cascader>
                        </div>
                    </el-col>
                </el-row>

                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>项目概况：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-input
                                type="textarea"
                                :rows="2"
                                placeholder="请输入内容"
                                v-model="save.projectoverview">
                            </el-input>
                        </div>
                    </el-col>
                </el-row>


                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>施工进度：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-input
                                type="textarea"
                                :rows="2"
                                placeholder="请输入内容"
                                v-model="save.progressconstruction">
                            </el-input>
                        </div>
                    </el-col>
                </el-row>


                <el-row class="tableItem">
                    <el-col :span="2">
                        <div class="grid-content bg-purple">
                            <p>业主通知：</p>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="grid-content bg-purple-light">
                            <el-input
                                type="textarea"
                                :rows="2"
                                placeholder="请输入内容"
                                v-model="save.noticeowner">
                            </el-input>
                        </div>
                    </el-col>
                </el-row>

                
                <el-row class="tableItem tabBtn">
                    <el-button type="primary" @click="submit">确认</el-button>
                    <el-button>清空</el-button>
                </el-row>
            </div>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
import cityJson from '@/assets/city.json';
export default {
    data () {
        return {
            line: [],
            site: [],
            city: [],
            chooseCity: {},
            save: {
                line: {},
                site: {},
                id: "", //
                noticeowner: "", // 业主通知
                progressconstruction: "", // 施工进展
                projectoverview: "", // 项目概况
                rid: "", // 站点id -- > save.site.id
                weatherid: "", // 天气id
                isdelete: "",
                geographical: "", // 地理位置 -- 废弃
                weather: "", // 天气 -- 废弃
            },
            options: [{
                value: '选项1',
                label: '黄金糕'
            }, {
                value: '选项2',
                label: '双皮奶'
            }],
            siteID: '',
            msg: {}
        }
    },
    methods: {
        // 改变线路
        changeLine(choose) {
            console.log(choose);
            this.save.line = choose;
            this.getLinesite(choose.id);
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log(res.data.list)
                this.line = res.data.list;
                this.save.line = res.data.list[0]
            })
        },
        // 改变站点
        changeSite(choose) {
            console.log(choose);
            this.save.site = choose;
            this.siteID = choose.id;
        },
        // 获取站点
        getLinesite(routeid) {
            api.getLinesite(routeid).then(res => {
                console.log(res.data.list)
                this.site = res.data.list;
                this.save.site = res.data.list[0];
                this.siteID = res.data.list[0].id;
            })
        },
        // 获取站点信息
        getMainpageinformaction(rid) {
            api.getMainpageinformaction(rid).then(res => {
                console.log(res.data);
                if(res.data.msg) {
                    this.save.id = res.data.msg.id;
                    this.save.noticeowner = res.data.msg.noticeowner;
                    this.save.progressconstruction = res.data.msg.progressconstruction;
                    this.save.projectoverview = res.data.msg.projectoverview;
                    this.save.weatherid = res.data.msg.weatherid;
                    this.chooseCity = [
                        'undefined',
                        'undefined',
                        res.data.msg.weatherid
                    ]
                } else {
                    this.save.id = "-1";
                    this.save.noticeowner = "";
                    this.save.progressconstruction = "";
                    this.save.projectoverview = "";
                    this.save.weatherid = "";
                }
            })
        },
        // 新增站点信息
        newMainpageinformaction (save) {
            api.newMainpageinformaction({
                noticeowner: save.noticeowner,
                progressconstruction: save.progressconstruction,
                projectoverview: save.projectoverview,
                rid: save.site.id,
                weatherid: save.weatherid
            }).then(res => {
                console.log(res)
                if(res.data.code == 200) {
                    this.$message({
                        type: 'success',
                        message: '站点设置成功！'
                    });
                }else {
                    this.$message({
                        type: 'warning',
                        message: '站点设置失败！'
                    });
                }
            })
        },
        // 修改站点信息
        changeMainpageinformaction(save) {
            api.changeMainpageinformaction({
                id: save.id,
                noticeowner: save.noticeowner,
                progressconstruction: save.progressconstruction,
                projectoverview: save.projectoverview,
                rid: save.site.id,
                weatherid: save.weatherid
            }).then(res => {
                console.log(res);
            })
        },
        // 提交判断
        submit() {
            console.log(this.save)
            console.log(this.city)
            console.log(this.chooseCity)
            if(!this.save.weatherid) {
                this.$message({
                    type: 'info',
                    message: '请选择城市'
                });
                return;
            }
            return;
            if(this.save.id == "-1") {
                this.newMainpageinformaction(this.save);
            } else {
                this.changeMainpageinformaction(this.save);
            }
        },
        changeCity(choose) {
            console.log(choose)
        }
    },
    watch: {
        siteID(val) {
            console.log(val)
            this.getMainpageinformaction(val);
        },
        chooseCity(val) {
            console.log(val)
            this.save.weatherid = val[2];
        }
    },
    created() {
        let _this = this;
        this.getSelectAll();
        setTimeout(() => {
            console.log(this.save)
            this.getLinesite(_this.save.line.id);
        }, 500)
        this.city = cityJson;
    },
}
</script>
<style lang="stylus" scoped>
.box
    background #fff
    border-radius 12px;
    overflow hidden
    .title
        margin 20px 20px 0 20px
        border-bottom 1px solid #eee
        padding-bottom 12px
    .tableCOn
        margin 0 20px 20px 20px
        padding-left 40px
        box-sizing border-box
        .tableItem
            display flex
            align-items center
            margin-bottom 20px
        .tabBtn
            justify-content flex-end



</style>



